<template>
	<div>
		<scroll-header></scroll-header>
		<div class="img_box">
		<gallary v-if="info.imgUrl.length"  :imgs="info.imgUrl" :class="[showGallery?'fullscreen gallery':'scroll_banner']" @click.native="showGalleryFun" :type="1"></gallary>
		<span class="close" v-if="showGallery" @click="showGalleryFun">关闭</span>
		</div>
		<div class="product_wrap">
			<!-- <div class="info_header"> -->
	
				<h6 class="product_name">
					<p class="ellipsis2">{{info.goods_title}}</p>
					<div>
					<b>(累计出租<i>{{info.buy_count}}</i>次)</b>
					<b>收藏<i>{{info.collect}}</i>次</b>
					<b>浏览<i>{{info.visits}}</i>次</b>
					</div>
				</h6>
			<!-- </div> -->
			<input type="hidden" :value="info.id" id="rid">
			<div class="info_main">
				<ul>
				<li>
						<div>
							<p><label>游戏区/服：</label>{{info.game_all_name}}</p>
							<p><label>租用金额：</label><span id="aprice">{{info.rent_price}}</span>元/小时</p>
						</div>
						<div>
							<p><label>上号方式：</label>{{info.is_show_text}}</p>
							<p><label>租用押金：</label>{{info.foregift}}元</p>
						</div>
					</li>
					<li class="li_type">
						<label>租用方式：</label>
						<div class="rent_type">
							<b class="choosed" way="1">时长</b>
						</div>
					</li>
						<li>
							<label>租用时间：</label>
							<div class="count show_type">
								<span class="des" @click="timeReduce"></span>
								<input type="number" class="num" v-model="chooseHour" :data-val="info.chooseHour" @input="mustInt" @blur="hasTrueValue">
								<span class="plus" @click="timeAdd"></span>
							</div>
							<span>租用金额：<i id="total_price">{{total_price}}</i>元</span>
						</li>
					<li>
						<span>温馨提示：<span style="color: #e6292c;">不得使用第三方软件，违反会扣除押金！</span></span>
					</li>
				</ul>
			</div>
			</div>
			<ul class="tab_menu">
				<li :class="{active:this.tab=='info'}" @click='switchPanel("info")'>账户信息</li>
				<li :class="{active:this.tab=='notice'}" @click='switchPanel("notice")'>租号须知</li>
			</ul>
			<div class="tab_con" v-show="this.tab=='info'">
				<table class="account_info">
					<tr>
						<td>所属游戏：{{info.big_game_name}}</td>
						<td>游戏大区：{{info.game_all_name}}</td>
					</tr>
					<tr>
						<td>游戏名字：{{info.role_name}}</td>
						<td>游戏等级：{{info.grade_id}}</td>
					</tr>
					<tr>
						<td>最短租用：{{info.short_lease}}小时</td>
						<td>可否排位：{{info['if_play_qualifying']==0?'允许':'不允许'}}</td>
					</tr>
				</table>
				<p class="section-title">号主描述</p>
				<div v-html="info.remark"></div>
			</div>
			<div class="tab_con" id="announce"  v-show="this.tab=='notice'">
				<p>注：如果租来的订单账号/密码/上号码错误或者不显示，请去租号订单记录里，找到该订单，点击“订单详情”查看最新获取的账号/密码/上号码。如有问题，请及时联系网站客服或投诉订单！</p>
				<ul>
					<li>1.用户请遵守平台用户租号协议，如果用户不遵守协议，比如在游戏中诈骗、发广告、毁号、恶意删除好友等，平台有权永久封停用户账号，如需赔偿账号损失，平台保留对用户法律起诉的权利。</li>
					<li>2.用户租号过程中请勿使用第三方软件(包含外挂、脚本、改游戏数据等软件)，平台将扣除用户租金和押金赔偿给号主，情节严重者将永久封停平台账号，如需赔偿账号损失，平台保留对用户法律起诉的权利。</li>
					<li>3.请用户文明游戏，请勿“逃跑”“挂机”“送人头”等可能导致账号被封的行为，如经核实用户出现以上情况，将扣除租金和押金给号主，情节恶意的将封停平台账号。</li>
					<li>4.针对恶意投诉者(如密码正确，投诉密码错误，玩一个小时投诉顶号，账号与描述不符等情况)，平台会查实，如属实将冻结平台账号</li>
					<li>5.用户在平台租号，如果投诉率和撤单率过高，平台会查实投诉情况，如果恶意投诉将被判定为恶意租号，将会受到相应的重复，严重者封停平台账号。</li>
					<li>6.商品详情界面会有属性显示是否允许打排位赛(比如LOL 王者荣耀)如号主设置不能打排位赛，用户打了排位，有押金的情况下会扣除押金给号主。</li>
				</ul>
			</div>
		<!-- </div> -->
		<!-- </section> -->
		<footer id="footer">
			<ul id="foodList">
				<router-link to="/Rent" tag="li">目录</router-link>
				<li><a @click="tips('该功能未开通')">客服</a></li>
				<li v-text="collects?'取消收藏':'收藏'" :class="{colled:collects}" @click="collectFun"></li>
				<li class="buy" @click="tips('无法进行真实租号哦')">马上租用</li>
			</ul>
		</footer>
	</div>
</template>
<script>
import scrollHeader from "common/scrollHeader.vue"
import tipShow from "@/assets/js/tips.js"
import gallary from "common/gallary.vue"
export default{
	data (){
		return {
			chooseHour:"",
			showGallery:false,
			collects:false,
			tab:'info',
			info:{
				imgUrl:[
					"static/img/rent-5883cb71c.jpg",
					"static/img/rent-9c6bd0517f.jpg",
					"static/img/rent-51bb538ad.jpg",
					"static/img/rent-f06f1960e.jpg",
					],
				goods_title:"屿妖♥V8六荣耀典藏飓风号全息星空杀手天鹅新增武则天♥白虎志♥冰霜恋舞曲♥冰封战神♥辉光之辰♥优雅恋人等 文明租号106信誉分扣分了是狗",
				buy_count:"45",
				visits:"1239",
				id:"",
				collect:56,
				location:"王者荣耀",
				if_play_qualifying:0,
				game_all_name:"王者荣耀",
				rent_price:"6.5",
				is_show_text:"账号密码登录",
				foregift:10,
				big_game_name:"",
				role_name:"",
				grade_id:"v8",
				short_lease:2,
				remark:"<p>注：租号详情页都直指向同一账号，就不重复耗时地操作去填写每一条数据啦~</p><p>♥首先感谢大家选择支持我的账号 ♥租号前先看清楚描述和截图满意您再租避免不必要的麻烦 账号保证如实描述每次上架前都会上号检查 别租了又说铭文不行、信誉分低打不了排位、没想象中那么好等理由维权 </p><p>♥租号后请不要消极挂机、辱骂他人、乱改名字、乱用背包道具和金币钻石、刷虚假骗人消息、不允许排位的账号不要去打排位。导致扣分和其他违规行为的一经发现提前改密码顶号提前维权结束租金押金不退 ♥租号期间有什么问题可以加卖家QQ或群查找“屿妖”进群联系我，尽量不要维权，核实有问题会补偿你。 </p><p>♥加好友或进群老客户可享受一定优惠价 感谢大家的支持 以后的日子里我会更加努力做得更好 让大家以实惠的价格租优质的账号</p>"
			},
			minimum:0		
		}
	},
	methods:{
		showGalleryFun (){
			this.showGallery=!this.showGallery;
			var cur=this.$route.fullPath
		},
		mustInt (){
			var intNum=/^([1-9]\d*|0)$/ ;
			var choose=this.chooseHour;
			// console.log(isNaN(this.chooseHour))
			// console.log(this.info.short_lease)
			// console.log(isNaN(this.info.short_lease))
			if(!intNum.test(choose)){
				this.chooseHour=parseInt(choose);
				// console.log(this.info.short_lease)
				tipShow("小时数必须为整数！")
			}
		},
		hasTrueValue (){
			if(this.chooseHour<this.minimum){
				tipShow("号主设置"+this.minimum+"小时起租")
				this.chooseHour=this.minimum
			}else if(!this.chooseHour){
				tipShow("不能为空")
				this.chooseHour=this.minimum
			}
			
		},
		collectFun (){
			this.collects=!this.collects;
			this.collects?tipShow('收藏成功'):tipShow('取消收藏成功')
		},
		timeAdd:function(){this.chooseHour++;},
		timeReduce:function() {
			if(this.chooseHour==this.minimum){
				tipShow("号主设置"+this.minimum+"小时起租")
			}else{
				this.chooseHour--;
			}
		},
		switchPanel:function(tab) {
			this.tab=tab;
		},
		tips (msg){
			tipShow(msg)
		}
	},
	mounted:function() {
		this.minimum=this.info.short_lease;
		this.chooseHour=this.info.short_lease
	},
	computed:{
		total_price:function() {
			var info=this.info;
			if(isNaN(Number(this.chooseHour))){
				return ""
			}else{
				return Number(info.rent_price)*this.chooseHour
			}
		}
	},
	components:{
		scrollHeader,
		gallary
	}
}
	
</script>
<style scoped>
	@import url("../../assets/css/rent_detail.css");
	.tab_con >>> div>p{
		text-indent: 2em;
		line-height: 0.21rem;
		margin: 10px 0px;
	 }
		 footer{
		     width: 100%;
		     padding: 0;
		     position: fixed;
		     bottom: 0px;
		     left: 0px;
		     background: #fff;
		     box-shadow: 0px -1px 3px rgba(88,88,88,0.2);
		}
		 footer > ul{
		     width: 100%;
		     display: flex;
		}
		 footer li{
		     height: 0.48rem;
		     width: 20%;
		     line-height: 0.74rem;
		     text-align: center;
		     background-size: 0.22rem;
		     background-position: center 0.05rem;
		     background-repeat: no-repeat;
		}
		 footer li:nth-of-type(1) {
		     background-image: url(../../assets/img/icon_category.png);
		}
		 footer li:nth-of-type(2) {
		     background-image: url(../../assets/img/icon_qq.png);
		}
		 footer li:nth-of-type(3) {
		     background-image: url(../../assets/img/icon_star.png) 
		}
		 footer li.colled {
		     background-image: url(../../assets/img/icon_stared.png) 
		}
		 footer li:nth-of-type(4) {
		     width: 40%;
		     color: #fff;
		     font-size: 0.16rem;
		     line-height: 0.48rem;
		     background-color: #e6292c;
		}
	 .scroll_banner{
	 	width: 100%;
	 	height: 0px;
	 	padding-bottom: 61%;
	 	overflow: hidden;
	 	position: relative;
	 }
	 .scroll_banner >>> img{
	 	width: 100%;
	 	object-fit: cover;
	 }
	 .gallery{
	 	z-index: 99;
	 	background: #000;
	 }
	 .gallery >>> img{
	 	object-fit: scale-down; 
	 	width: 100%;
	 	height: 100vh;
	 }	 
	 .scroll_banner >>>.swiper-pagination1{
	 	color: #eee;
	 	font-size: 12px;
	 	width: 40px;
	 	text-align: center;
	 	left: auto;
	 	background: rgba(0,0,0,0.3);
	 	border-radius: 12px;
	 	right: 20px;
	 	height: 18px;
	 	line-height: 18px;
	 	bottom: 30px;
	 	transition:none;
	 }
	 .gallery >>>.swiper-pagination1{
	 	color: #fff;
	 	font-size: 12px;
	 	bottom: 60px;
	 	transition:none;
	 }
	 .close{
	 	z-index: 100;
	 	position: fixed;
	 	left: 20px;
	 	top: 30px;
	 	color: #bbb;
	 	background: rgba(0,0,0,0.3);
	 	border-radius: 12px;
	 	right: 20px;
	 	height: 18px;
	 	line-height: 18px;
	 }
</style>


